@import url(https://fonts.googleapis.com/css2?family=Inter:wght@400;600&family=Lobster&display=swap);
* {
    font-family: 'Inter', sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}
html {
    scroll-behavior: smooth;
}
:root {
    --theme-color: #d14081;
    --violet: #ea7af4;
    --russian-violet: #320e3b;
    --midnight-green-eagle-green: #1a535c;
    --tart-orange: #f0544f;
    --white: #f0ebeb;
    --bubble-duration: 0.5s;
}
#symptoms li {
    background-color: transparent;
}
#floating_img_1 {
    position: absolute;
    width: 6rem;
    left: -2rem;
    bottom: 1rem;
    z-index: 1;
}
#floating_img_2 {
    position: absolute;
    width: 6rem;
    right: 0rem;
    top: 3rem;
    z-index: 1;
}
#floating_img_3 {
    position: absolute;
    width: 6rem;
    left: -2rem;
    bottom: 1rem;
    z-index: 1;
}
#floating_img_4 {
    position: absolute;
    width: 6rem;
    right: -2rem;
    top: 0.1rem;
    z-index: 1;
}
#floating_img_5 {
    position: absolute;
    width: 6rem;
    right: 0.5rem;
    top: 5rem;
    z-index: 1;
}
#world_data {
    position: relative;
}
#world_data .row {
    position: relative;
}
#world_data .row::after {
    content: '';
    position: absolute;
    height: 90%;
    left: 50%;
    top: 50%;
    -webkit-transform: translateY(-50%);
    transform: translateY(-50%);
    width: 2px;
    background-color: #bbbaba;
}

.about p {
    line-height: 2.5rem;
    font-size: 1.1rem;
    text-align: left;
}
#white_img {
    position: absolute;
    top: 6rem;
    z-index: -1;
    left: -4rem;
    width: 10rem;
    -webkit-animation: rotate 12s linear infinite;
    animation: rotate 12s linear infinite;
}
#live-cases {
    position: relative;
}
#live-cases a:before {
    content: '';
    position: absolute;
    left: 0%;
    top: 40%;
    background-color: #d63535;
    border-radius: 50%;
    height: 10px;
    width: 10px;
    -webkit-animation: flash 0.5s infinite alternate ease-in-out;
    animation: flash 0.5s infinite alternate ease-in-out;
}
@-webkit-keyframes flash {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}
@keyframes flash {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}
#country_list {
    max-height: 300px;
    overflow: auto;
}
#country_wise_cases {
    position: relative;
}
#country_list li {
    color: #320e3b;
    color: var(--russian-violet);
}
#country_list li:hover {
    background-color: #e8ebeb !important;
    cursor: pointer;
}

#country_cases div div:nth-child(1) .jumbotron {
    height: 200px;
    background: url(/Coronavirus-Tracker/static/media/brushed_alum.c6842476.png),
        linear-gradient(to bottom, #e22b59, #db5975);
}
#country_cases div div:nth-child(2) .jumbotron {
    height: 200px;
    background: url(/Coronavirus-Tracker/static/media/brushed_alum.c6842476.png),
        linear-gradient(to bottom, #447acf, #1b4392);
}
#country_cases div div:nth-child(3) .jumbotron {
    height: 200px;
    background: url(/Coronavirus-Tracker/static/media/brushed_alum.c6842476.png),
        linear-gradient(to bottom, #5a5858, #747272);
}
#country_cases div div:nth-child(4) .jumbotron {
    height: 200px;
    background: url(/Coronavirus-Tracker/static/media/brushed_alum.c6842476.png),
        linear-gradient(to bottom, #7e3caa, #b15bd3);
}
#submit_btn {
    position: relative;
    z-index: 10;
    outline: none;
    background-color: #f8f9fa;
    border-radius: 0.5rem;
    border: 4px solid #d14081;
    border: 4px solid var(--theme-color);
    display: -ms-grid;
    display: grid;
    place-items: center;
    color: #d14081;
    color: var(--theme-color);
}
#submit_btn span {
    border-radius: 0.4rem;
    border: none;
    position: absolute;
    width: 110%;
    outline: none;
    height: 130%;
    background-color: transparent;
    z-index: -1;
    overflow: hidden;
    transition: all 0.3s ease-in;
}
#submit_btn:hover {
    color: #f0ebeb;
    color: var(--white);
    transition: color 0.3s ease-in;
}
#submit_btn span::after {
    content: '';
    position: absolute;
    top: 50%;
    left: 70%;
    -webkit-transform: skew(30deg) translate(-50%, -50%);
    transform: skew(30deg) translate(-50%, -50%);
    width: 30%;
    height: 200%;
    background-color: #f8f9fa;
    border: none;
    outline: none;
    transition: all 0.3s ease-in;
}

#submit_btn:hover span::after {
    background-color: #d14081;
    background-color: var(--theme-color);
    outline: none;
    width: 200%;
    transition: all 0.3s ease-in;
}
#submit_btn:focus {
    outline: none !important;
    outline-offset: none !important;
    box-shadow: none !important;
    -webkit-box-shadow: none !important;
}
#world_data .col-md-3:nth-child(-n + 3)::after {
    content: '';
    position: absolute;
    right: 0;
    top: 50%;
    -webkit-transform: translateY(-50%);
    transform: translateY(-50%);
    background-color: #bbbaba;
    width: 2px;
    height: 50%;
}
button:disabled,
select:disabled {
    cursor: not-allowed;
    pointer-events: auto;
}

.chart-wrapper {
    width: 100%;
    height: 60vh;
}
@media (max-width: 768px) {
    #world_data .col-md-3:nth-child(-n + 3)::after {
        z-index: -1;
    }
    .heading {
        font-size: 2rem;
    }
    .main-jumbotron {
        margin: 0;
        border-radius: 0;
    }
    #needs_help {
        padding: 0;
    }
}
@media (min-width: 768px) {
    #world_data .row::after {
        z-index: -1;
    }
}
@media (max-width: 576px) {
    html {
        font-size: 0.8rem;
    }
    .about p {
        line-height: 2.5rem;
        font-size: 1.2rem;
        text-align: left;
    }
    #floating_img_1 {
        left: 1rem;
        bottom: -2rem;
    }
    #floating_img_2 {
        right: 1rem;
        top: 3rem;
    }
    #floating_img_3 {
        left: 1rem;
        bottom: 1rem;
    }
    #floating_img_4 {
        right: 5px;
        top: 5px;
    }
}
@media (max-width: 350px) {
    html {
        font-size: 0.7rem;
    }
    .about p {
        line-height: 2.5rem;
        font-size: 1.2rem;
        text-align: left;
    }
    #floating_img_1 {
        left: 1rem;
        bottom: -2rem;
    }
    #floating_img_2 {
        right: 1rem;
        top: 3rem;
    }
    #floating_img_4 {
        right: 5px;
        top: 5px;
    }
}
#check_symptoms {
    border-radius: 2rem;
    background-color: #d14081;
    background-color: var(--theme-color);
    color: #f3efef;
    outline: none;
    box-shadow: 2px 2px 2rem #ee8db9;
    transition: color 0.2s ease-in, -webkit-transform 0.2s ease-in;
    transition: transform 0.2s ease-in, color 0.2s ease-in;
    transition: transform 0.2s ease-in, color 0.2s ease-in, -webkit-transform 0.2s ease-in;
    transition: transform 0.2s ease-in, color 0.2s ease-in,
        -webkit-transform 0.2s ease-in;
}
#check_symptoms:hover {
    background-color: #eeecec;
    color: #d14081;
    color: var(--theme-color);
    -webkit-transform: translateY(-5px);
    transform: translateY(-5px);
    transition: color 0.2s ease-in, -webkit-transform 0.2s ease-in;
    transition: transform 0.2s ease-in, color 0.2s ease-in;
    transition: transform 0.2s ease-in, color 0.2s ease-in, -webkit-transform 0.2s ease-in;
    transition: transform 0.2s ease-in, color 0.2s ease-in,
        -webkit-transform 0.2s ease-in;
}

.ellipses {
    width: 40px;
    height: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
}
.ellipse {
    border-radius: 50%;
    border-color: #d14081;
    border-color: var(--theme-color);
    border-width: 0px;
    width: 0;
    height: 0;
    background-color: #d14081;
    background-color: var(--theme-color);
    border-style: solid;
}
.bubble-wrapper {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #dddddd;
    position: absolute;
    width: 100%;
    height: 100%;
}
#ellipse-1 {
    -webkit-animation: bubble 0.5s infinite alternate ease-in-out;
            animation: bubble 0.5s infinite alternate ease-in-out;
    -webkit-animation: bubble var(--bubble-duration) infinite alternate ease-in-out;
            animation: bubble var(--bubble-duration) infinite alternate ease-in-out;
}
#ellipse-2 {
    -webkit-animation: bubble 0.5s 0.1s infinite alternate ease-in-out;
            animation: bubble 0.5s 0.1s infinite alternate ease-in-out;
    -webkit-animation: bubble var(--bubble-duration) 0.1s infinite alternate ease-in-out;
            animation: bubble var(--bubble-duration) 0.1s infinite alternate ease-in-out;
}
#ellipse-3 {
    -webkit-animation: bubble 0.5s 0.2s infinite alternate ease-in-out;
            animation: bubble 0.5s 0.2s infinite alternate ease-in-out;
    -webkit-animation: bubble var(--bubble-duration) 0.2s infinite alternate ease-in-out;
            animation: bubble var(--bubble-duration) 0.2s infinite alternate ease-in-out;
}
#ellipse-4 {
    -webkit-animation: bubble 0.5s 0.3s infinite alternate ease-in-out;
            animation: bubble 0.5s 0.3s infinite alternate ease-in-out;
    -webkit-animation: bubble var(--bubble-duration) 0.3s infinite alternate ease-in-out;
            animation: bubble var(--bubble-duration) 0.3s infinite alternate ease-in-out;
}
#ellipse-5 {
    -webkit-animation: bubble 0.5s 0.4s infinite alternate ease-in-out;
            animation: bubble 0.5s 0.4s infinite alternate ease-in-out;
    -webkit-animation: bubble var(--bubble-duration) 0.4s infinite alternate ease-in-out;
            animation: bubble var(--bubble-duration) 0.4s infinite alternate ease-in-out;
}
@-webkit-keyframes bubble {
    from {
        border-width: 0;
    }

    to {
        border-width: 15px;
    }
}
@keyframes bubble {
    from {
        border-width: 0;
    }

    to {
        border-width: 15px;
    }
}

.progress {
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    z-index: 5;
    height: 5px;
    border-radius: 0;
}
.progress-bar {
    background-color: #d14081;
    background-color: var(--theme-color);
}
.precautions img {
    border-radius: 1rem;
    box-shadow: -7px -7px 10px #ffffff, 3px 3px 10px #00000063;
    object-fit: contain;
    padding: 0;
}
.precautions {
    transition: -webkit-transform 0.3s ease-in-out;
    transition: transform 0.3s ease-in-out;
    transition: transform 0.3s ease-in-out, -webkit-transform 0.3s ease-in-out;
    padding: 2rem;
    margin: 4rem 0;
    color: #320e3b;
    color: var(--russian-violet);
}
.precautions:hover {
    box-shadow: 5px -5px 20px #ffffff67, 2px 2px 10px #0000002a;
    -webkit-transform: translateY(-5px);
    transform: translateY(-5px);
    transition: -webkit-transform 0.3s ease-in-out;
    transition: all 0.3s ease-in-out;
    transition: all 0.3s ease-in-out, -webkit-transform 0.3s ease-in-out;
}
.precautions h5 {
    transition: all 0.2s ease-in;
}
.precautions:hover h5 {
    color: #d14081;
    color: var(--theme-color);
    transition: color 0.2s ease-in;
}
.content h5,
.content p {
    line-height: 2rem;
}
.content p {
    color: #1a535c;
    color: var(--midnight-green-eagle-green);
}
.content {
    margin: 3rem 0;
    padding: 1rem;
}
.heading {
    color: #d14081;
    color: var(--theme-color);
}
#needs_help {
    position: relative;
}
.title {
    color: #d14081;
    color: var(--theme-color);
}
#needs_help::after {
    content: '';
    z-index: -1;
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 50%;
    background-color: #d14081;
    background-color: var(--theme-color);
}
.footer {
    min-height: 40vh;
    background-color: #d14081;
    background-color: var(--theme-color);
}
.footer-main-content .sub-heading {
    color: #f0ebeb;
    color: var(--white);
}
.footer-main-content ul a {
    text-decoration: none;
    background: transparent;
    color: #f0ebeb;
    color: var(--white);
    border: 0px;
    transition: color 0.2s ease-in;
    display: flex;
    align-items: center;
}
.footer-main-content ul a:hover {
    color: #320e3b;
    color: var(--russian-violet);
    transition: color 0.2s ease-in;
}
@-webkit-keyframes rotate {
    from {
        -webkit-transform: rotate(0deg);
        transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(360deg);
        transform: rotate(360deg);
    }
}
@keyframes rotate {
    from {
        -webkit-transform: rotate(0deg);
        transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(360deg);
        transform: rotate(360deg);
    }
}

